html,body{
  height: 100%;
}
* {
  box-sizing: border-box;
}
.btns{
  display: block;
}
.tls{
  display: none;
}
.hd{
  width: 24%;
  height: 60%;
}
.vid{
  width: 200px;
  height: 200px;
}
.adj{
  width: 130px;
}
.txt{
  font-family: Arial, Helvetica, sans-serif ;
  font-weight: 700;
  color: #4A164B;
  letter-spacing: 2px;
}
.stl{
  font-family: Arial, Helvetica, sans-serif ;
  
  color: #5E5F5F;
}
@media only screen and (max-width:600px){
  .vid{
    width: 80px;
    height: 80px;
  }
  .hd{
    width: 50px;
  }
  .adj{
    width: 40px;
  }
}